<template>
  <v-overlay>

  <v-row align="center" justify="center">
    <v-col cols="12">
        <v-card
          class="mx-auto"
          max-width="96%"
          color="tertiary"
        >
          <br>
          <h2
          class="text-h2 text-center mb-5">
          Let's make you a Premium user!
          </h2>

            <v-spacer></v-spacer>
            <v-btn
              color="secondary"
              dark fab small top right absolute
              @click="doPremiumUpgradeAction('premium-upgrade-close')"
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>


          <p class="text-h6 font-weight-light text-center mb-5">
          Start enjoying the benefits of a Premium user now for just ${{ premPlan[ premPlan.current ][0] }}/month billed {{ premPlan.current.toLowerCase() }}. You can cancel anytime.
          </p>
          <v-row align="center" justify="center">
            <v-col cols="6" >

              <v-row align="center" justify="center">
                <v-col class="ma-1" cols="6">

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn style="width: 100%;"
                        :color = "( premPlan.current === 'Yearly' ) ? 'primary' : 'white'"
                        :outlined = "( premPlan.current === 'Yearly' ) ? false : true"
                        @click="togglePlan"
                        rounded x-large
                  v-bind="attrs"
                  v-on="on"
                      >$11 a Month<br>
                      Billed Yearly</v-btn>

                    </template>
                    <span>Save 20%</span>
                  </v-tooltip>

                </v-col>
                <v-col class="ma-1" cols="6">
                  <v-btn style="width: 100%;"
                    :color = "( premPlan.current === 'Monthly' ) ? 'primary' : 'white'"
                    :outlined = "( premPlan.current === 'Monthly' ) ? false : true"
                    @click="togglePlan"
                    rounded x-large
                  >$14 a Month<br>
                  Billed Monthly</v-btn>
                </v-col>
              </v-row>

            </v-col>
            <v-col cols="6" >
              <ul class="text-h6 font-weight-light">
                <li>Real-time Alerts</li>
                <li>Create Custom Reports</li>
                <li>Export data to CSV / PDF</li>
                <!-- <li>2 years of email history</li> -->
                <li>Advanced data filtering</li>
                <li>Custom Business Hours</li>
                <li>Remove automated messages</li>
                <!-- <li>Team and individual performance reports</li> -->
              </ul>
            </v-col>
          </v-row>
          <br>
          <v-divider class="mx-4"></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn class="mr-2 ml-2" color="secondary"
            rounded  elevation="2"
            @click="doPremiumUpgradeAction('pay-card-' + premPlan.current.toLowerCase() )"
            >
            <v-icon left>mdi-credit-card-outline</v-icon>
            Pay ${{ premPlan[ premPlan.current ][1] }}/{{ premPlan.current }} with Card
            </v-btn>
            <v-btn class="mr-2 ml-2" color="secondary"
            rounded elevation="2"
            @click="doPremiumUpgradeAction('pay-paypal-' + premPlan.current.toLowerCase() )"
            >
            <span style="color: #009CDE">Pay</span><span style="color: #003087">Pal</span>
            </v-btn>

            <v-spacer></v-spacer>
            <v-btn
              icon outlined
              @click="toggleText = !toggleText"
            >
              <v-icon>{{ toggleText ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>

          </v-card-actions>
          <v-divider class="mx-6"></v-divider>
<div class="text-center  mt-2 mb-2">
